<template>
  <span>
    <Button shape="circle" @click="logOut"></Button>
  </span>
</template>
<script>
  import {mapState, mapMutations} from 'vuex'
  import {config} from '../config/dev'

  export default {
    data () {
      return {
      }
    },
    methods: {
      ...mapMutations([
        'logout'
      ]),
      logOut () {
        config.HTTP.get(
          `/auth/logout`
        )
          .then(response => {
            console.log(response.data)
            this.$Message.success('')
            this.logout()
          })
          .catch(error => {
            console.log(error)
            this.$Message.error('')
          })
      }
    },
    computed: {
      ...mapState([
        'isLogin', 'user'
      ])
    }
  }
</script>




