<template>
  <span>
    <Button type="primary" shape="circle" @click="modal = true"></Button>
    <Modal
      v-model="modal"
      title=""
      :loading="loading"
      :closable="closable"
      :maskClosable="maskClosable"
      @on-cancel="modal = false"
      @on-ok="handleSubmit('registerForm')">

    <Form @keydown.left.native.stop="" @keydown.right.native.stop="" @keyup.enter.native="handleSubmit('registerForm')"
          ref="registerForm" :model="registerForm" :rules="registerRule" :label-width="80">
        <Form-item label="" prop="username">
            <Input v-model="registerForm.username" placeholder="" :autofocus="true"></Input>
        </Form-item>
        <Form-item label="" prop="email">
            <Input v-model="registerForm.email" placeholder=""></Input>
        </Form-item>
        <Form-item label="" prop="password1">
            <Input v-model="registerForm.password1" type="password"
                   placeholder=""></Input>
        </Form-item>
        <Form-item label="" prop="password2">
            <Input v-model="registerForm.password2" type="password"
                   placeholder=""></Input>
        </Form-item>
    </Form>
      <div style="padding-left: 88%; margin:0 0">
        <Button type="ghost" @click="handleReset('registerForm')"></Button>
      </div>
    </Modal>
  </span>
</template>


<script>
  import {config} from '../config/dev'

  export default {
    data () {
      const passwordCheck = (rule, password2, callback) => {
        if (password2 !== this.registerForm.password1) {
          callback(new Error('!'))
        } else {
          callback()
        }
      }

      const emailCheck = (rule, email, callback) => {
        config.HTTP.get(
          `/users/auto-search?email=${email}`
        )
          .then(response => {
            if (response.status === 204) {
              // add a validation pass callback
              callback()
            } else if (response.status === 200) {
              callback(new Error(''))
            }
          })
          .catch(error => {
            callback(error)
          })
      }
      const usernameCheck = (rule, username, callback) => {
        config.HTTP.get(
          `/users/auto-search?username=${username}`
        )
          .then(response => {
            if (response.status === 204) {
              // add a validation pass callback
              callback()
            } else if (response.status === 200) {
              callback(new Error(''))
            } else {
              callback(response.data)
            }
          })
          .catch(error => {
            callback(error)
          })
      }

      return {
        modal: false,
        loading: true,
        closable: true,
        maskClosable: false,

        registerForm: {
          username: '',
          email: '',
          password1: '',
          password2: ''
        },

        registerRule: {
          username: [
            {required: true, message: '', trigger: 'blur'},
            {validator: usernameCheck, trigger: 'blur'}
          ],
          email: [
            {required: true, message: '', trigger: 'blur'},
            {type: 'email', message: '', trigger: 'blur'},
            {validator: emailCheck, trigger: 'blur'}
          ],
          password1: [
            {required: true, message: '', trigger: 'blur'},
            {type: 'string', min: 6, message: '6', trigger: 'blur'}

          ],
          password2: [
            {required: true, message: '', trigger: 'blur'},
            {validator: passwordCheck, trigger: 'blur'}
          ]
        }
      }
    },
    methods: {
      handleSubmit (name) {
        this.loading = true
        this.$refs[name].validate((valid) => {
          if (valid) {
            config.HTTP.post(
              '/auth/register',
              {
                name: this.registerForm.username,
                pass: this.registerForm.password2,
                email: this.registerForm.email
              })
              .then(response => {
                if (response.status === 201) {
                  this.modal = false
                  this.$Message.success('!')
                  this.$refs[name].resetFields()
                }
              })
              .catch(error => {
                console.log(error)
                this.modal = false
                this.$Message.error('!')
              })
          } else {
            this.$Message.error('!')
          }
        })
      },
      handleReset (name) {
        this.$refs[name].resetFields()
      }
    }
  }
</script>




