<template>
  <span>
    <Button shape="circle" @click="showModal"></Button>
    <Modal1
      title=""
      :value="modal"
      :maskClosable="maskClosable"
      ok-text=""
      :loading="loading"
      :closable="closable"
      @on-cancel="modal = false"
      @on-ok="handleSubmit('formInline')">
      <Form @keydown.left.native.stop="" @keydown.right.native.stop="" @keyup.enter.native="handleSubmit('formInline')"
            ref="formInline"
            :model="formInline" :rules="ruleInline" style="width: 80%; margin-left: 10%">
        <Form-item prop="user">
        <Input type="text" v-model="formInline.user" placeholder="" :autofocus="true">
          <Icon type="ios-person-outline" slot="prepend"></Icon>
          </Input>
        </Form-item>
        <Form-item prop="password">
          <Input type="password" v-model="formInline.password" placeholder="">
          <Icon type="ios-locked-outline" slot="prepend"></Icon>
          </Input>
        </Form-item>
      </Form>
      <div style="padding-left: 88%; margin:0 0">
        <Button type="ghost" @click="handleReset('formInline')"></Button>
      </div>
    </Modal1>
  </span>
</template>
<script>
  import showdown from 'showdown'
  import {EventBus} from '../store/EventBus'
  import Modal1 from './Modal'
  import jwtDecode from 'jwt-decode'
  import {mapState, mapMutations} from 'vuex'
  import {config} from '../config/dev'

  let converter = new showdown.Converter()
  export default {
    components: {
      Modal1
    },
    data () {
      return {
        modal: false,
        loading: true,
        closable: false,
        maskClosable: false,
        searchMessage: '',
        formInline: {
          user: '',
          password: ''
        },
        ruleInline: {
          user: [
            {required: true, message: '', trigger: 'blur'}
          ],
          password: [
            {required: true, message: '', trigger: 'blur'},
            {type: 'string', min: 6, message: '6', trigger: 'blur'}
          ]
        }
      }
    },
    methods: {
      ...mapMutations([
        'login'
      ]),
      showModal () {
        this.modal = true
      },
      handleSubmit (name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            this.value = true
            this.loading = true
            config.HTTP.post(
              `/auth/login`,
              {
                username: this.formInline.user,
                password: this.formInline.password
              })
              .then((response) => {
                if (response.status === 200) {
                  this.modal = false
                  this.$Message.success('!')
                  let user = jwtDecode(response.data.token).user
                  // this must be the former one
                  localStorage.setItem('jwtToken', response.data.token)
                  this.login({user: user, isLogin: true})
                } else if (response.status === 401) {
                  this.$Message.error('')
                }
              })
              .catch(error => {
                if (error.response.status === 401) {
                  this.$Message.error('')
                }
              })
          } else {
            this.loading = false
            this.modal = true
            this.$Message.error('!')
          }
        })
      },

      handleReset (name) {
        this.$refs[name].resetFields()
      }
    },
    computed: {
      search () {
        EventBus.$on('search-text', (searchText) => {
          this.searchMessage = converter.makeHtml('#' + searchText)
          console.log(this.searchMessage)
        })
        return this.searchMessage
      },
      ...mapState([
        'isLogin', 'user'
      ])
    }
  }
</script>




